
import Blur from 'vux/src/components/blur/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

import ScoreItem from '@/components/ScoreItem'
import {mapGetters, mapMutations} from 'vuex'
export default {
  head: {
    title: {
      inner: ''
    }
  },
  data () {
    return {
      logout: false
    }
  },
  computed: {
    ...mapGetters({
      userInfo: 'getUserInfo'
    })
  },
  components: {
    Blur,
    XImg,
    ScoreItem,
    Group,
    Cell,
    XButton,
    Confirm,
    Scroller
  },
  methods: {
    selectBlur () {
      if (!this.userInfo) {
        return 'static/img/blur.jpg'
      } else {
        return 'static/img/blur1.jpg'
      }
    },
    handleLogout () {
      this.$vux.toast.show({
        type: 'text',
        width: '20em',
        text: '',
        time: '1000'
      })
      this.postLogout(this)
    },
    ...mapMutations({
      postLogout: 'postLogout'
    })
  }
}



